/* header */
.header{
  padding-top: 100px;
  padding-bottom: 100px;
}
#boo{
  font-size: 70px;
  color: black;
  padding-top: 5%;
  

}

#boo p{
  font-size: 70px;
  animation-delay: 20s;
  animation:20s fadeIn;
  animation-fill-mode: forwards;
  transition: opacity ;
}

@keyframes fadeIn {
 
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}



